import {ValidationTemplate} from "../Apps/IdpSelfService/CommonInterfaces/oidc-validation";
import {UserChoices} from "../Apps/IdpSelfService/CommonInterfaces/user-choices";
import forge from 'node-forge';

function parseToken (token :string) {
    // eslint-disable-next-line no-magic-numbers
    const base64Url = token.split('.')[1];
    // eslint-disable-next-line require-unicode-regexp
    const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    const jsonPayload = JSON.parse(decodeURIComponent(window.atob(base64).split('')
        .map(
            (c) => {
                // eslint-disable-next-line no-magic-numbers
                return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
            }
        )
        .join('')));
    return jsonPayload;
}

export function isAdminUser (token :string) {
    return parseToken(token).is_org_admin;
}


export function getOrgId (token :string) {
    return parseToken(token).org_id;
}

interface Updatable{
    [key:string]: string
}

/**
 *
 * @param {*} value
 * @param {*} key
 * @param {*} stateObject
 * @param {*} updateFunction
 */
// eslint-disable-next-line max-params
export function setStateValue (value :string, key :string, stateObject:Object|undefined, updateFunction :Function) {

    const updatedValue :Updatable = {};
    updatedValue[key] = value;
    const updated = {
        ...stateObject,
        ...updatedValue
    }

    updateFunction(updated);
}

interface VerificationResult {
    status: string;
    message: string;
}

const isBase64 = (str: string): boolean => {
    if (!(/^[A-Za-z0-9+/]*={0,2}$/u).test(str) || str.length % 4 !== 0) {
        return false;
    }

    try {
        const decoded = forge.util.decode64(str);
        const reEncoded = forge.util.encode64(decoded);
        return reEncoded === str;
    } catch {
        return false;
    }
};

/**
 *
 * @param {String} input value to be checked based on the provided type
 * @param {String} type effectively and enum used to specify the type of check we need to run
 * @param {boolean} required is this a required field
 * @returns String message to be relayed to the user pertaining to the invalid input
 * // success, warning, error, or default
 */
export function verifyInput (input:string, type:string, required:boolean) :VerificationResult {
    const INVALID_CERT_MESSAGE = 'This is invalid, please check for uncommonly used characters. Be sure to exclude the start and end block';
    const INVALID_ENTITYID_MESSAGE = 'This is invalid. This field is intended to consist of only alphanumeric characters and valid URL\'s'
    const EXPIRED_CERT_MESSAGE = 'Certificate is expired';
    const INVALID_TEXT_MESSAGE = 'This text is invalid please check for uncommonly used characters or spaces';
    const INVALID_URL_MESSAGE = 'This field needs to be a valid URL';

    if (required && !input) {
        return {
            status: 'error',
            message: type === 'RADIO' ? 'A selection must be made' : 'This field can not be left empty'
        }
    }

    let verified: VerificationResult;
    let results: boolean;

    switch (type) {
        case 'GENERAL':
            results = !(/[^A-Za-z0-9]/u).test(input);
            verified = {
                status: results ? 'default' : 'error',
                message: results ? '' : INVALID_TEXT_MESSAGE
            }
            break;
        case 'ENTITYID':
            results = (/^[a-z0-9._:/?#@!&()*+,;=-]+$/iu).test(input);
            verified = {
                status: results ? 'default' : 'error',
                message: results ? '' : INVALID_ENTITYID_MESSAGE
            }
            break;
        case 'DOMAIN':
            // eslint-disable-next-line
            results = (/^https:\/\/([a-zA-Z0-9-]+(\.[a-zA-Z0-9-]+)+)(:\d{1,5})?(\/\S*)?$|^http:\/\/localhost(:\d{1,5})?(\/\S*)?$/gu).test(input)
            verified = {
                status: results ? 'default' : 'error',
                message: results ? '' : INVALID_URL_MESSAGE
            }
            break;
        case 'x509':
            try {
                // strip out white spaces and line breaks
                const sanitizedCertChainArray = input.replace(/[\s\n\r]/gu, '').split(',');

                // check if all cert chain is properly encoded in base64
                for (const cert of sanitizedCertChainArray) {
                    if (!isBase64(cert.trim())) {
                        return {
                            status: 'error',
                            message: INVALID_CERT_MESSAGE
                        };
                    }
                }

                // check cert expiration (verify only the first cert in the chain)
                const now = new Date();
                const firstCertInTheChain = sanitizedCertChainArray[0].trim();
                const certWithHeaders = `-----BEGIN CERTIFICATE-----\n${firstCertInTheChain.match(/.{1,64}/gu)?.join('\n')}\n-----END CERTIFICATE-----`
                const cert = forge.pki.certificateFromPem(certWithHeaders);

                if (now < cert.validity.notBefore || now > cert.validity.notAfter) {
                    return {
                        status: 'error',
                        message: EXPIRED_CERT_MESSAGE
                    }
                }

                verified = {
                    status: 'default',
                    message: '',
                }
            } catch (e) {
                return {
                    status: 'error',
                    message: INVALID_CERT_MESSAGE
                }
            }
            break;
        default:
            verified = {
                status: 'default',
                message: ''
            }
    }

    return verified;
}

/**
 * 
 * @param {Object} choices state object containing all of the users input data
 * @param {Object} validations validations object pre-set to govern what the inputs are intended to be
 * @param {function} setValidations function used to update active validations.
 * @param {String} currentState variable being changed
 * @return {boolean} represents the current pass fail state for the provided validation set
 */
// eslint-disable-next-line
export function isFailureTriggered (choices :UserChoices, currentState :string, validations: ValidationTemplate, setValidations: Function) {
    let failureTriggered = false;
    const update: ValidationTemplate = {};
    Object.keys(validations).forEach(validationKey => {
        update[validationKey] = {};
        if (validationKey === currentState) {
            Object.keys(validations[currentState]).forEach(field => {

                const submittedValue = choices && choices.selections[field];
                // @ts-ignorer
                const submittedType = validations[validationKey][field].inputType;

                // @ts-ignore
                const verificationObject: VerificationResult = verifyInput(submittedValue, submittedType, true);

                if (verificationObject.status !== 'default') {
                    failureTriggered = true;
                }

                // @ts-ignore
                update[validationKey][field] = {
                    inputType: submittedType,
                    variant: verificationObject.status,
                    message: verificationObject.message
                }
            });
        }
    });

    setValidations(update);
    return failureTriggered;
}

export const getSSOBaseUrl = (chromeApi: any) => {
    if (chromeApi.getEnvironment() === 'prod') {
        return "https://sso.redhat.com/auth/realms/redhat-external/";
    }
    return "https://sso.stage.redhat.com/auth/realms/redhat-external/";
}

export const getSSOApiUrl = (chromeApi: any, apiPath: string) => {
    return getSSOBaseUrl(chromeApi) + apiPath;
}

export const setupBackTriggerNotification = () => {
    window.addEventListener("popstate", () => {
        // eslint-disable-next-line no-alert
        alert("Please note that use of the back arrow on this page will cause you to lose your currently configured data");
    })
}
